<template>
  <div class="seek-result">
    <!--  -->
    <div>
      <ul v-if="orders?.length">
        <p></p>
        <img
          v-if="loading"
          src="../assets/playing.gif"
          style="margin: 30rem auto; display: block"
          alt=""
        />

        <li
          v-for="(o, i) in orders"
          :key="i"
          v-show="o == 'artist' || o == 'album' || o == 'playlist'"
        >
          <figure v-if="o != 'new_mlog'">
            <img :src="album[o][0][ablumIndex[o]?.img]" />
          </figure>

          <div
            class="artist"
            @click="
              $router.push({
                path: '/artist',
                query: {
                  id: album[o][0].id,
                  albumId: album['album'] ? album['album'][0].id : '',
                },
              })
            "
          >
            <h4 ref="h4" v-if="o != 'new_mlog' && o == 'artist'">
              {{ album[o][0][ablumIndex[o]?.title] }}:
              {{ album[o][0][ablumIndex[o]?.name] }}
              {{
                album[o][0][ablumIndex[o]?.fName][0]
                  ? "(" + album[o][0][ablumIndex[o]?.fName][0] + ")"
                  : ""
              }}
            </h4>
          </div>

          <div
            v-if="o != 'new_mlog' && o == 'album'"
            class="album"
            @click="
              $router.push({ path: '/album', query: { id: album[o][0].id } })
            "
          >
            <h4 ref="h4">
              {{ album[o][0][ablumIndex[o]?.title] }}:
              {{ album[o][0][ablumIndex[o]?.name] }}
            </h4>

            <p ref="p">{{ album[o][0][ablumIndex[o]?.fName][0]?.name }}</p>
          </div>

          <div
            class="playlist"
            @click="
              $router.push({ path: '/playlist', query: { id: album[o][0].id } })
            "
            v-if="o != 'new_mlog' && o == 'playlist'"
          >
            <h4 ref="h4">{{ ablumIndex[o].title }}: {{ album[o][0].name }}</h4>

            <p ref="p">by {{ album[o][0][ablumIndex[o]?.fName].nickname }}</p>
          </div>
          <i></i>
        </li>
      </ul>
    </div>

    <!--  -->
    <img
      v-if="loading"
      src="../assets/playing.gif"
      style="margin: 30rem auto; display: block"
      alt=""
    />
    <ul class="multimatch-list">
      <li
        v-for="(multimatch, index) in multimatchs"
        :key="index"
        @click="
          $emit('update-song', multimatch);
          $emit('update-song-list', multimatchs);
        "
        :class="{ active: playingId == multimatch.id }"
      >
        {{ multimatch.name }}
        <p>
          <span class="sq" v-if="multimatch.sq"></span
          >{{ multimatch.ar[0].name }} - {{ multimatch.al.name }}
        </p>
        <span v-if="playingId !== multimatch.id" class="play"> </span>
        <span v-else class="play active" :class="{ playing: playing }">
          <span></span>
          <span></span>
          <span></span>
          <span></span>
        </span>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data: () => ({
    loading: false,
    album: [],
    orders: [],
    songResult: [],
    ablumIndex: {
      artist: {
        name: "name",
        title: "occupation",
        img: "img1v1Url",
        fName: "alias",
      },
      album: {
        name: "name",
        title: "type",
        img: "picUrl",
        fName: "artists",
      },
      playlist: {
        name: "name",
        title: "",
        img: "coverImgUrl",
        fName: "creator",
      },
    },
  }),
  props: {
    multimatchs: Array,
    value: String,
    playing: Boolean,
    playingId: [String, Number],
  },
  created() {
    this.loading = true;

    let s = this.axios("https://apis.netstart.cn/music/cloudsearch", {
      params: {
        keywords: this.value,
        limit: 30,
        offset: 0,
      },
    });

    let z = this.axios("https://apis.netstart.cn/music/search/multimatch", {
      params: {
        keywords: this.value,
      },
    });
    Promise.all([z, s]).then(([z, s]) => {
      this.loading = false;

      this.album = z.data.result;
      this.songResult = s.data.result?.songs;
      this.orders = z.data.result?.orders; //    console.log(z.data.result.artist[0].alias[0]);
      // console.log("", z.data.result); //    console.log("", s.data.result.songs);
    });
  },
};
</script>

<style lang="less" scoped>
.seek-result {
  padding: 8rem 0 4rem 0;
  > div:nth-of-type(1) {
    > p {
      padding-left: 10rem;
      color: #888;
    }
    > ul {
      padding: 0 10rem;
      > li {
        display: flex;
        align-items: center;
        height: 50rem;
        padding: 8rem 10rem 8rem 0;
        box-sizing: content-box;
        figure {
          flex-shrink: 0;
          width: 50rem;
          height: 50rem;
          margin-right: 15rem;
          > img {
            width: 100%;
          }
        }
        h4 {
          width: 260rem;
          text-overflow: ellipsis;
          white-space: nowrap;
          overflow: hidden;
        }
        > i {
          flex-shrink: 0;
          display: inline-block;
          background-image: url("data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHZpZXdCb3g9IjAgMCAxNiAyNiI+PHBhdGggZmlsbD0ibm9uZSIgc3Ryb2tlPSIjOWI5YjliIiBzdHJva2Utd2lkdGg9IjMiIHN0cm9rZS1saW5lY2FwPSJzcXVhcmUiIHN0cm9rZS1taXRlcmxpbWl0PSIxMCIgZD0iTTIuOTM5IDIzLjkzOUwxMy44NzkgMTMgMi45MzkgMi4wNjEiLz48L3N2Zz4=");
          background-position: center;
          background-repeat: no-repeat;
          background-size: cover;
          width: 8rem;
          height: 13rem;
          margin: 0 8rem 0 10rem;
        }
        .artist {
          > h4 {
            height: 30rem;
            line-height: 30rem;
            font-size: 17rem;
          }
        }
        .album {
          > h4 {
            height: 30rem;
            line-height: 30rem;
            font-size: 17rem;
          }
          > p {
            height: 16rem;
            color: #999;
            line-height: 16rem;
            font-size: 12rem;
          }
        }
        .playlist {
          > h4 {
            height: 30rem;
            line-height: 30rem;
            font-size: 17rem;
          }
          > p {
            height: 16rem;
            color: #999;
            line-height: 16rem;
            font-size: 12rem;
          }
        }
      }
    }
  }
}
ul.multimatch-list {
  li {
    padding: 8rem 0;
    border-bottom: 1rem solid rgba(0, 0, 0, 0.082);
    margin-left: 10rem;
    position: relative;
    font-size: 16rem;

    span.song-name {
      font-size: 15rem;
      word-wrap: nowrap;
      overflow: hidden;
      /*  */
      text-overflow: ellipsis;
      /*  */
    }
    span.play {
      display: block;
      background-image: url("../assets/index_icon_2x.png");
      background-position: -24px 0;
      background-size: 166px 97px;
      width: 22rem;
      height: 22rem;
      position: absolute;
      right: 10rem;
      top: 50%;
      margin-top: -11rem;
      &.active {
        background-image: none;
        // display: flex;
        justify-content: space-around;
        span {
          width: 11%;
          height: 100%;
          background: #d43c33;
          animation: playingrunning 0.6s infinite alternate;
          animation-play-state: paused;
          transform-origin: bottom;
          &:nth-child(1) {
            animation-delay: 0s;
            margin-left: 2rem;
          }
          &:nth-child(2) {
            animation-delay: -0.2s;
            margin-left: 8rem;
          }
          &:nth-child(3) {
            animation-delay: -0.4s;
            margin-left: 14rem;
          }
          &:nth-child(4) {
            animation-delay: -0.6s;
            margin-left: 20rem;
          }
        }

        &.playing {
          span {
            animation-play-state: running;
          }
        }
      }
    }
    span.sq {
      position: relative;
      display: inline-block;
      margin-right: 3rem;
      background: url("../assets/index_icon_2x.png") no-repeat 0 0;
      background-size: 166px 97px;
      width: 12px;
      height: 8px;
    }
    p {
      color: rgba(0, 0, 0, 0.447);
      margin-top: 5rem;
      font-size: 12rem;
    }
  }
}
</style>