<template>
  <div class="playlist">
    <ul class="card" :class="{ lightUl: !show }">
      <p>
        
        <span style="color: hsla(0, 0%, 100%, 0.6)">
          {{ "(" + songlist?.length + ")" }}
        </span>
      </p>
      <li v-for="song in songlist" :key="song.id" :style="{ color: song.id == songId ? '#d43c33' : 'white' }" @click="
        $emit('update-song', song);
        $emit('toggle-showPlayList');
      ">
        <img v-show="song.id == songId" src="../assets/playing.gif" width="12" alt="" />
        {{ song.name }}
        <span class="singername" :style="{
          color: song.id == songId ? '#d43c33' : 'hsla(0, 0%, 100%, 0.6)',
        }">
          -
          {{ song.song ? song.song.artists[0]?.name : song.ar[0]?.name }}
        </span>

        <i class="close" @click.stop="$emit('delete-song', song)">X</i>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    songlist: Array,
    songId: [Number, String],
    show: Boolean,
  },
};
</script>

<style lang="less" scoped>
.playlist {
  width: 100%;
  height: 60vh;
  position: fixed;
  bottom: 80rem;
  left: 0;
  z-index: 9999;
  padding: 15rem;
  overflow: hidden;
  color: white;

  i.close {
    float: right;
    font-size: 12rem;
    position: relative;
    top: 4rem;
    color: white;
    opacity: 0.6;
    z-index: 11111;
    display: block;
  }

  p {
    margin-bottom: 10rem;
  }

  .card {
    &.lightUl {
      background: white;
      box-shadow: 0 0 10px 5px rgba(143, 143, 143, 0.366);

      p,
      li {
        color: black !important;
      }

      span {
        color: rgb(107, 104, 104) !important;
      }

      i {
        color: #6b6868;
      }
    }

    height: 350rem;
    background-color: #282828;
    min-height: calc(60vh - 30rem);
    border-radius: 15rem;
    padding: 20rem;
    overflow-y: auto;
    scrollbar-width: none;
    box-shadow: 0 0 10px 5px rgba(0, 0, 0, 0.366);

    &::-webkit-scrollbar {
      display: none;
    }

    li {
      padding: 6rem;
      font-size: 14rem;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;


      span.singername {
        font-size: 12rem;
      }
    }
  }
}
</style>