<template>
  <ul class="songs-details">
    <p></p>
    <li
      v-for="(singlesong, index) in singlesongs"
      :key="index"
      @click="$emit('update-song', singlesong);$emit('update-song-list', singlesongs)"
      :class="{ active: playingId == singlesong.id }"
    >
      <span class="rank">{{ index + 1 }}</span>
      <span class="song-name">{{ singlesong.name }}</span>
      <p>
        {{ singlesong.ar[0].name }}/{{ singlesong.ar[1]?.name }} -
        {{ singlesong.name }}
      </p>
      <span v-if="playingId !== singlesong.id" class="play"> </span>
      <span v-else class="play active" :class="{ playing: playing }">
        <span></span>
        <span></span>
        <span></span>
        <span></span>
      </span>
    </li>
    <h3 v-if="album?.type != ''"></h3>
  </ul>
</template>

<script>
export default {
  props: {
    singlesongs: Array,
    album: Object,
    playing: Boolean,
    playingId: [String, Number],
  },
};
</script>

<style lang="less" scoped>
.songs-details {
  background: white;
  padding-bottom: 20rem;
  h3 {
    text-align: center;
    padding: 5rem 0;
    color: #999;
    font-size: 14px;
  }
  > p {
    background: #eeeff0;
    padding: 2rem 15rem 3rem;
  }
  background: #f8f8f8;
  li {
    padding: 8rem 0;
    border-bottom: 1rem solid rgba(0, 0, 0, 0.082);
    margin-left: 10rem;
    position: relative;
    left: 30rem;
    &:last-child {
      border: 0;
    }
    span.sq {
      display: inline-block;
      margin-right: 3rem;
      background: url("../assets/index_icon_2x.png") no-repeat 0 0;
      background-size: 166px 97px;
      width: 12px;
      height: 8px;
    }
    span.rank {
      width: 16rem;
      text-align: center;
      display: block;
      text-align:center;
      width:20rem;
      position: absolute;
      left: -30rem;
      top: 50%;
      margin-top: -5rem;
      font-size: 16rem;
      color: rgba(0, 0, 0, 0.477);
    }
    span.song-name {
      font-size: 15rem;
    }

    span.play {
      display: block;
      background-image: url("../assets/index_icon_2x.png");
      background-position: -24px 0;
      background-size: 166px 97px;
      width: 22rem;
      height: 22rem;
      position: absolute;
      right: 40rem;
      top: 50%;
      margin-top: -11rem;
      &.active {
        background-image: none;
        display: flex;
        justify-content: space-around;
        > span {
          flex-shrink: 0;
          width: 11%;
          height: 100%;
          background: #d43c33;
          animation: playingrunning 0.6s infinite alternate;
          animation-play-state: paused;
          transform-origin: bottom;
          &:nth-child(1) {
            animation-delay: 0s;
          }
          &:nth-child(2) {
            animation-delay: -0.2s;
          }
          &:nth-child(3) {
            animation-delay: -0.4s;
          }
          &:nth-child(4) {
            animation-delay: -0.6s;
          }
        }

        &.playing {
          span {
            animation-play-state: running;
          }
        }
      }
    }
    p {
      color: rgba(0, 0, 0, 0.447);
      margin-top: 5rem;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      width: 80vw;
    }
  }
}
</style>