<template>
  <div
    class="PlayBar"
    @click="
      $router.push({
        path: '/lyric',
        query: { id: selectSongs.id },
      });
      showPlayList ? $emit('toggle-showPlayList') : '';
    "
  >
    <div class="left">
      <img
        :class="{ playing: playing }"
        :src="
          selectSongs?.picUrl
            ? `${selectSongs.picUrl}?imageView&thumbnail=360x0`
            : `${selectSongs.al.picUrl}?imageView&thumbnail=360x0`
        "
        alt=""
      />
      <p>
        {{ selectSongs?.name }}
      </p>
    </div>

    <div class="right">
      <button v-if="!playing" @click.stop="$emit('song-play')">
        <i class="fa fa-play"></i>
      </button>
      <button v-else @click.stop="$emit('song-pause')">
        <i class="fa fa-pause"></i>
      </button>
      <canvas ref="canvas" width="30" height="30"></canvas>

      <button @click.stop="$emit('toggle-showPlayList')">
        <i class="fa fa-bars"></i>
      </button>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    selectSongs: Object,
    playing: Boolean,
    duration: Number,
    currentTime: Number,
    model: String,
    showPlayList: Boolean,
  },
  computed: {
    percentage: function () {
      return this.currentTime / this.duration;
    },
  },
  watch: {
    percentage: function (n) {
      if (this.selectSongs) {
        var canvas = this.$refs.canvas;
        var context = canvas.getContext("2d");

        context.clearRect(0, 0, 30, 30);

        context.beginPath();
        context.arc(15, 15, 14, -0.5 * Math.PI, Math.PI * (2 * n - 0.5));
        context.strokeStyle = "white";
        context.stroke();
      }
    },
  },
};
</script>

<style lang="less" scoped>
@keyframes rotate {
  form {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.PlayBar {
  width: 100vw;
  height: 60rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #1a1a1a;
  position: fixed;
  bottom: 0;
  padding: 0 20rem;
  z-index: 10;

  .left {
    display: flex;
    align-items: center;
    color: #efefef;
    font-size: 14rem;
    p {
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap;
      width: 65%;
    }
    img {
      border-radius: 50rem;
      position: relative;
      border: 9rem solid rgb(0, 0, 0);
      box-sizing: content-box;
      outline: 0;
      top: -15rem;
      margin-right: 10rem;
      width: 50rem;
      height: 50rem;
      animation: rotate 8s linear infinite;
      animation-play-state: paused;
      &.playing {
        animation-play-state: running;
      }
    }
  }

  .right {
    display: flex;
    position: relative;
    canvas {
      position: relative;
      // z-index: 999;
    }
    button {
      position: absolute;
      z-index: 999;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      margin: auto;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      background: none;
      border: 0;
      color: white;
      &:last-child {
        width: 50rem;
        height: 50rem;
        background-repeat: no-repeat;
        background-position: 15px;
        background-size: 20px;
        margin-right: 30rem;
      }
    }
  }
}
</style>