<template>
  <li
    class="hotsong-item"
    @click="
      $emit('update-song', hotsong);
      $emit('update-song-list', hotsongs);
    "
    :class="{ active: playingId == hotsong.id }"
  >
    <span class="rank">{{ rank }}</span>
    <span class="song-name">{{ hotsong?.name }}</span>
    <p>
      <span class="sq" v-if="hotsong.sq"></span>{{ hotsong?.ar[0].name }} -
      {{ hotsong?.al.name }}
    </p>
    <span v-if="playingId !== hotsong.id" class="play"> </span>
    <span v-else class="play active" :class="{ playing: playing }">
      <span></span>
      <span></span>
      <span></span>
      <span></span>
    </span>
  </li>
</template>

<script>
export default {
  props: {
    hotsong: Object,
    hotsongs: Array,
    index: Number,
    playing: Boolean,
    playingId: [String, Number],
  },
  computed: {
    rank: function () {
      return (this.index + 1).toString().padStart(2, "0");
    },
  },
};
</script>

<style lang="less" scoped>
li.hotsong-item {
  padding: 8rem 0;
  border-bottom: 1rem solid rgba(0, 0, 0, 0.082);
  margin-left: 10rem;
  position: relative;
  left: 30rem;
  &:nth-child(-n + 3) {
    span.rank {
      color: #d43c33;
    }
  }
  span.sq {
    display: inline-block;
    margin-right: 3rem;
    background: url("../assets/index_icon_2x.png") no-repeat 0 0;
    background-size: 166px 97px;
    width: 12px;
    height: 8px;
  }
  span.rank {
    position: absolute;
    left: -30rem;
    top: 50%;
    margin-top: -5rem;
    font-size: 16rem;
    color: rgba(0, 0, 0, 0.477);
  }
  span.song-name {
    font-size: 15rem;
  }
  span.play {
    display: block;
    background-image: url("../assets/index_icon_2x.png");
    background-position: -24px 0;
    background-size: 166px 97px;
    width: 22rem;
    height: 22rem;
    position: absolute;
    right: 40rem;
    top: 50%;
    margin-top: -11rem;
    &.active {
      background-image: none;
      display: flex;
      justify-content: space-around;
      span {
        width: 11%;
        height: 100%;
        background: #d43c33;
        animation: playingrunning 0.6s infinite alternate;
        animation-play-state: paused;
        transform-origin: bottom;
        &:nth-child(1) {
          animation-delay: 0s;
        }
        &:nth-child(2) {
          animation-delay: -0.2s;
        }
        &:nth-child(3) {
          animation-delay: -0.4s;
        }
        &:nth-child(4) {
          animation-delay: -0.6s;
        }
      }

      &.playing {
        span {
          animation-play-state: running;
        }
      }
    }
    span.playing {
      background-size: cover;
      background-image: url("../assets/playing.gif");
    }
  }
  p {
    color: rgba(0, 0, 0, 0.447);
    margin-top: 5rem;
  }
}
</style>