<template>
  <div class="lyric">
    <div class="loadingImg" v-if="loading">
      <img
        src="../assets/playing.gif"
        style="margin: auto; display: block"
        alt=""
      />
    </div>

    <!--  -->
    <div class="bg"></div>

    <!--  -->
    <header class="playing_title">
      <i
        class="fa fa-reply"
        @click="
          $router.push(fromPath);
          $emit('toggle-showPlayLyric');
          showPlayList ? $emit('toggle-showPlayList') : '';
        "
      ></i>
      <div class="playing_song">
        <p class="songname">{{ songDetails?.name }}</p>
        <p class="singername">{{ songDetails?.ar[0].name }}</p>
      </div>
      <i class="fa fa-share-square-o"></i>
    </header>

    <!--  -->
    <section class="playing_main">
      <PlayLyric
        v-if="showlyric"
        @click.native="showlyric = false"
        :lyricID="songId"
        :currentTime="currentTime"
        :playing="playing"
      ></PlayLyric>
      <PlayDics
        v-else
        :songDetails="songDetails"
        :playing="playing"
        @click.native="showlyric = true"
      ></PlayDics>
    </section>

    <!--  -->
    <footer class="playing_features">
      <!--  -->
      <section class="features_progressBar">
        <span>{{
          Math.floor($refs?.input?.value / 60)
            .toString()
            .padStart(2, "0") +
          ":" +
          ($refs?.input?.value % 60).toFixed(0).toString().padStart(2, "0")
        }}</span>
        <label>
          <div class="Progressbar">
            <div class="time" :style="`width:${jindu}%`">
              <i class="point"></i>
            </div>
          </div>
          <input
            type="range"
            ref="input"
            min="0"
            step="0.01"
            :max="duration"
            :value="draging ? $refs.input.value : tmpCurrentTime"
            @input="
              draging = true;
              updateInputValue = false;
              updateInput();
            "
            @change="
              draging = false;
              $emit('update-time', $event.target.value);
            "
          />
        </label>
        <span>{{
          Math.floor(duration / 60)
            .toString()
            .padStart(2, "0") +
          ":" +
          (duration % 60).toFixed(0).toString().padStart(2, "0")
        }}</span>
      </section>

      <!--  -->
      <section class="features_btns">
        <button
          class="play-model"
          @click="$emit('change-model')"
          :style="
            model !== 'sequence'
              ? `background-image: url('./assets/${model}.png');`
              : `background-image: url('./assets/${model}.png');background-size:40px;background-position: 0px 1px`
          "
        ></button>
        <button @click="$emit('change-song', 'prev')">
          <i class="fa fa-step-backward"></i>
        </button>
        <button
          v-if="!playing"
          @click.stop="
            currentTime > 0
              ? $emit('update-time', currentTime) && $emit('song-play')
              : $emit('song-play')
          "
        >
          <i class="fa fa-play"></i>
        </button>
        <button v-else @click.stop="$emit('song-pause')">
          <i class="fa fa-pause"></i>
        </button>
        <button @click="$emit('change-song', 'next')">
          <i class="fa fa-step-forward"></i>
        </button>
        <button @click="$emit('toggle-showPlayList')">
          <i class="fa fa-bars"></i>
        </button>
      </section>
    </footer>
  </div>
</template>

<script>
import PlayDics from "@/components/PlayDics.vue";
import PlayLyric from "@/components/PlayLyric.vue";

export default {
  components: {
    PlayDics,
    PlayLyric,
  },
  props: {
    playing: Boolean,
    currentTime: Number,
    duration: Number,
    model: String,
    show: Boolean,
    songId: [Number, String],
    showPlayList: Boolean,
    selectSongs: Object,
  },
  computed: {
    jindu: function () {
      if (this.draging || !this.updateInputValue) {
        return (this.inputValue / this.duration) * 100;
      } else {
        return (this.tmpCurrentTime / this.duration) * 100;
      }
    },
    fromPath() {
      return sessionStorage.getItem("fromPath");
    },
  },
  data() {
    return {
      songDetails: null,
      showlyric: false,
      tmpCurrentTime: this.currentTime,
      draging: false,
      lyricID: null,
      loading: false,
      updateInputValue: true,
      inputValue: this.currentTime,
    };
  },
  beforeRouteEnter(to, from, next) {
    let a = "";
    for (var q in from.query) {
      a += `${q}=${from.query[q]}&`;
    }
    if (a.length > 0) {
      sessionStorage.setItem("fromPath", from.path + "?" + a);
    } else {
      sessionStorage.setItem("fromPath", from.path);
    }
    next();
  },
  watch: {
    currentTime: function (newTime) {
      if (!this.draging) {
        this.updateInputValue = true;
        this.tmpCurrentTime = newTime;
      }
    },
    songId: function (n) {
      this.show ? "" : this.$emit("toggle-showPlayLyric");
      n;
      this.getSongDetails(n);
      this.$emit("song-play");
    },
  },
  methods: {
    // 
    getSongDetails(ids) {
      this.axios
        .get("https://apis.netstart.cn/music/song/detail", {
          params: {
            ids,
          },
        })
        .then((res) => {
          this.songDetails = res.data.songs[0];
          this.$emit("song-play");
        });
    },
    updateInput() {
      this.inputValue = this.$refs.input.value;
    },
  },
  beforeRouteUpdate(to, from, next) {
    this.show ? "" : this.$emit("toggle-showPlayLyric");
    this.getSongDetails(to.query.id);
    next();
  },
  created() {
    this.show ? "" : this.$emit("toggle-showPlayLyric");
    this.getSongDetails(this.$route.query.id);
  },
};
</script>

<style lang="less" scoped>
.custom-classes-transition {
  transition: all 3s;
}
.lyric {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  color: white;
  .loadingImg {
    width: 100%;
    height: 100vh;
    position: absolute;
    z-index: 111;
    background: white;
    img {
      position: absolute;
      top: 35%;
      left: 38%;
      width: 100rem;
      height: 100rem;
    }
  }
  i {
    color: white;
    font-size: 18rem;
  }
  .bg {
    width: 100%;
    height: 100%;
    position: absolute;
    background: #1d1d1d;
    z-index: -1;
  }
  .playing_title {
    width: 100%;
    height: 80rem;
    padding: 30rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .playing_song {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 70%;
      .songname {
        font-size: 16rem;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        width: 100%;
        text-align: center;
      }
      .singername {
        margin-top: 5rem;
        color: hsla(0, 0%, 100%, 0.6);
      }
    }
  }
  .playing_main {
    width: 100%;
    flex-grow: 1;
  }
  .playing_features {
    height: 25%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0 30rem;
    .features_progressBar {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      position: relative;
      height: 30%;
      span {
        color: hsla(0, 0%, 100%, 0.6);
        display: block;
        width: 10%;
      }
      label {
        width: 75%;
        height: 3px;
        position: relative;
        display: block;
        border-radius: 30px;
        // overflow: hidden;
        .Progressbar {
          width: 100%;
          height: 100%;
          background: hsla(0, 0%, 100%, 0.6);
          border-radius: 30px;
          position: absolute;
          z-index: -1;
          .time {
            width: 0%;
            height: 100%;
            border-radius: 30px;
            background: white;
            position: relative;
            i.point {
              position: absolute;
              top: -3rem;
              right: -4rem;
              display: block;
              width: 8rem;
              height: 8rem;
              background: white;
              border-radius: 50%;
            }
          }
        }

        input {
          width: calc(100% + 15px);
          position: absolute;
          top: -7px;
          left: -8px;
          display: block;
          opacity: 0;
        }
      }
    }
    .features_btns {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      height: 50%;
      button {
        background: none;
        width: 40rem;
        height: 40rem;
        border-radius: 50%;
        border: 0;
        font-size: 20rem;
        &:nth-child(3) {
          border: 1rem solid white;
          outline: 0;
          width: 50rem;
          height: 50rem;
        }
        &.play-model {
          background: transparent;
          // background-image: url("../assets/sequence.png");
          background-repeat: no-repeat;
          background-size: 20rem;
          background-position: 11px 11px;
        }
      }
    }
  }
}
</style>
