<template>
  <div class="artist">
    <section class="hd">
      <div class="artist-title">
        <i class="fa fa-reply" @click="$router.push('/search')" />
      </div>
      <img
        v-if="artistArr?.artist"
        :src="`${artistArr?.artist?.cover}?imageView&thumbnail=360x0`"
        alt=""
      />
      <div class="content">
        <h2>{{ artistArr?.artist?.name }}</h2>
        <h4 v-if="artistArr?.artist?.rank?.rank">
           No.{{ artistArr?.artist?.rank?.rank }}
        </h4>
        <div class="nick">
          <p v-if="artistArr?.user?.nickname">
            : {{ artistArr?.user?.nickname }}
          </p>
          <p class="fs">
            <span v-if="fans?.followCnt || fans?.followCnt === 0"
              > {{ fans?.followCnt }}&nbsp;&nbsp; |</span
            >
            &nbsp;&nbsp;
            {{
              fans?.fansCnt >= 10000
                ? fans?.fansCnt >= 100000000
                  ? (fans?.fansCnt / 100000000).toFixed(1) + ""
                  : "" + (fans?.fansCnt / 10000).toFixed(1) + ""
                : fans?.fansCnt
            }}
          </p>
          <p>
            <span v-if="artistArr?.artist?.identifyTag != null"
              >{{ artistArr?.artist?.identifyTag[0] }}
            </span>
            <span
              class="skill"
              v-for="(i, index) in artistArr?.artist?.identities"
              :key="index"
              >{{ i }}</span
            >
          </p>
        </div>
      </div>
    </section>
    <h2></h2>
    <div class="intro">
      <p :class="introClass">{{ artistArr?.artist?.briefDesc }}</p>
      <div
        v-show="introClass == 'shou'"
        @click="introClass = 'zhang'"
        class="full"
      >
        <i class="arrow"></i>
      </div>
      <div
        v-show="introClass == 'zhang'"
        @click="introClass = 'shou'"
        class="full"
      >
        <i class="arrow"></i>
      </div>
    </div>

    <h2></h2>
    <img
      v-if="loading"
      src="../assets/playing.gif"
      style="display: block; margin: 30rem auto"
      alt=""
    />
    <ul>
      <ArtistHotSong
        v-for="(h, i) in hotSong"
        :key="h.id"
        :hotSong="hotSong"
        :n="h"
        :index="i"
        @update-song="$emit('update-song', $event)"
        @update-song-list="$emit('update-song-list', $event)"
        :playing="playing"
        :playingId="playingId"
      ></ArtistHotSong>
    </ul>

    <h2 v-if="albumObj?.album?.blurPicUrl"></h2>
    <div
      v-if="`${albumObj?.album?.blurPicUrl}?imageView&thumbnail=360x0`"
      class="albumClass"
      @click="
        $router.push({ path: '/album', query: { id: albumObj?.album?.id } })
      "
      :playing="playing"
      :playingId="playingId"
    >
      <figure>
        <img
          v-if="albumObj?.album?.blurPicUrl"
          :src="`${albumObj?.album?.blurPicUrl}?imageView&thumbnail=360x0`"
          alt=""
        />
      </figure>
      <div class="content">
        <h4>{{ albumObj?.album?.name }}</h4>
        <p>{{ albumObj?.album?.artist?.name }}</p>
        <p>
          <span>{{ albumObj?.songs?.length }} </span>
          {{ new Date(albumObj?.album?.publishTime).toLocaleDateString() }}
        </p>
      </div>
    </div>
    <div class="artistFooter">
      <a href="javascript:void(0)"></a>
    </div>
    <div style="height: 32rem"></div>
  </div>
</template>

<script>
import ArtistHotSong from "../components/ArtistHotSong.vue";
import get from "../axiosApi/getArtist";
export default {
  props: {
    playing: Boolean,
    playingId: [String, Number],
  },
  components: {
    ArtistHotSong,
  },
  data: () => ({
    artistArr: [],
    fans: {},
    introClass: "shou",
    hotSong: [],
    albumObj: {},
    loading: false,
  }),
  created() {
    this.loading = true;
    get(this.$route.query.id).then((res) => {
      this.artistArr = res.data.data;
    });
    this.axios("https://apis.netstart.cn/music/artist/follow/count", {
      params: { id: this.$route.query.id },
    }).then((res) => {
      this.fans = res.data.data;
    });
    this.axios("https://apis.netstart.cn/music/artist/top/song", {
      params: { id: this.$route.query.id },
    }).then((res) => {
      this.loading = false;

      this.hotSong = res.data.songs.slice(0, 5);
    });
    if (this.$route.query.albumId) {
      this.axios
        .get("https://apis.netstart.cn/music/album", {
          params: { id: this.$route.query.albumId },
        })
        .then((res) => {
          this.albumObj = res.data;
        });
    }
  },
};
</script>

<style lang="less" scoped>
div.artist {
  background-color: #fcfcfd;

  .hd {
    width: 100%;
    position: relative;

    &::after {
      content: "";
      background-color: rgba(0, 0, 0, 0.18);
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
    }

    .artist-title {
      position: absolute;
      padding: 15em 10rem 5rem;
      color: white;
      z-index: 999;

      i.fa-reply {
        position: absolute;
        display: block;
        top: 20rem;
        left: 13rem;
        font-size: 20rem;
      }
    }

    > img {
      width: 100%;
      display: block;
    }

    .content {
      position: absolute;
      left: 16rem;
      bottom: 30rem;
      z-index: 2;
      color: white;

      h2 {
        height: 20rem;
        line-height: 20rem;
        font-size: 20rem;
      }

      h4 {
        font-size: 13rem;
        margin-top: 15rem;
      }

      > div.nick {
        margin-top: 15rem;
        opacity: 0.8;
        font-size: 12rem;
        line-height: 12rem;

        > p:not(:first-child) {
          margin-top: 10rem;
        }

        .fs {
          font-weight: 500;

          > span {
            font-weight: 400;
          }
        }

        p {
          .skill {
            display: inline-block;
            opacity: 0.6;
            border-radius: 4rem;
            padding: 2rem;
            border: 1rem solid white;
            transform: scale(0.7);
            margin-left: -4px;
          }
        }
      }
    }
  }

  > h2 {
    font-size: 18rem;
    z-index: 2;
    margin-top: -5rem;
    padding: 15rem;
    background: #fff;
    font-weight: 700;

    &.h2Show {
      > span {
        color: #888;
        font-size: 12rem;
        font-weight: 400;
        margin-left: 10rem;
      }
    }
  }

  .intro {
    background-color: #fff;
    padding: 0 16rem 18rem;
    color: #333;

    > p.shou {
      color: #666;
      overflow: hidden;
      text-overflow: ellipsis;
      -webkit-line-clamp: 3;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-text-size-adjust: 100%;
      font-size: 13.5rem;
    }

    > p.zhang {
      color: #666;
      font-size: 14rem;
      line-height: 20rem;
    }

    > div.full {
      font-size: 14rem;
      color: #666;
      text-align: center;
      margin-top: 25rem;

      > i {
        display: inline-block;
        position: relative;
        width: 9rem;
        height: 9rem;
        background: url(data:image/png;base64,);
        background-size: contain;
        background-repeat: no-repeat;
        left: 4rem;
        top: 0;
      }
    }
  }

  .albumClass {
    display: flex;
    padding: 0 15rem 20rem;

    > figure {
      width: 80rem;
      height: 80rem;
      flex-shrink: 0;
      margin-right: 25rem;
      position: relative;

      &::after {
        position: absolute;
        content: "";
        width: 90px;
        height: 100%;
        background: url("https://s3.music.126.net/mobile-new/img/album@3x.png?0ec33f3%E2%80%A6");
        background-size: contain;
        background-repeat: no-repeat;
        right: -10px;
      }

      img {
        width: 80rem;
      }
    }

    > div.content {
      > h4 {
        font-size: 15rem;
      }

      > p:first-of-type {
        font-size: 13rem;
      }

      p {
        color: #888;

        &:last-child {
          margin-top: 10rem;
        }
      }
    }
  }

  .more-concert {
    padding-top: 23rem;
    padding-bottom: 23rem;
    background: #fff;
    font-size: 14rem;
    color: #666;
    text-align: center;

    > i {
      -webkit-transform: rotate(-90deg);
      -ms-transform: rotate(-90deg);
      transform: rotate(-90deg);
      width: 12rem;
      height: 12rem;
      top: 2rem;
      display: inline-block;
      position: relative;
      background: url("data:image/png;base64,");
      background-size: contain;
      background-repeat: no-repeat;
      left: 4rem;
    }
  }

  .artistFooter {
    position: fixed;
    bottom: -8rem;
    left: 0;
    width: 100%;
    background-color: white;
    border-top: 1px solid rgb(231, 231, 231);
    display: flex;
    justify-content: center;
    align-items: flex-end;
    height: 62rem;
    padding: 11rem 30rem;

    > a {
      display: inline-block;
      width: 100%;
      height: 40rem;
      background-color: #ff3a3a;
      color: white;
      font-size: 18rem;
      border-radius: 100vw;
      text-align: center;
      line-height: 40rem;
      text-decoration: none;
    }
  }
}
</style>
