<template>
  <div class="searching">
    <p>"{{ value }}"</p>
    <img v-if="loading" src="../assets/playing.gif" alt="" />

    <ul v-if="!loading">
      <li
        v-for="searches in searching"
        :key="searches.id"
        @click="$emit('getResults', searches.keyword)"
      >
        <span class="search-glass"></span>
        {{ searches.keyword }}
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    value: String,
    searching: Array,
    loading: Boolean,
  },
};
</script>

