.searching {
  padding: 10rem 0 0 10rem;
  img {
    display: block;
    margin: 30rem auto;
  }
  p {
    font-size: 16rem;
    padding-bottom: 10rem;
    border-bottom: 1rem solid rgba(0, 0, 0, 0.048);
    color: #507daf;
  }
  ul {
    li {
      padding: 10rem 0;
      border-bottom: 1rem solid rgba(0, 0, 0, 0.048);
      position: relative;
      left: 30rem;
      font-size: 14rem;
      span.search-glass {
        left: -25rem;
        top: 13rem;
        background-image: url("data:image/svg+xml;base64,");
      }
    }
  }
