<template>
  <div class="playlist">
    <!--  -->
    <div
      v-if="playlist?.coverImgUrl"
      class="bg"
      :style="playlist && `backgroundImage:url(${playlist?.coverImgUrl})`"
    ></div>

    <div class="playlist-title">
      <i class="fa fa-reply" @click="$router.push(fromPath)" />
      <span></span>
    </div>

    <!--  -->
    <header>
      <!--  -->
      <div class="cover">
        <span class="earphone">{{ playcount + "" }}</span>
        <!-- <i></i> -->
        <img
          v-if="playlist?.coverImgUrl"
          :src="`${playlist?.coverImgUrl}?imageView&thumbnail=360x0`"
          width="166"
          alt=""
        />
      </div>
      <!--  -->
      <div class="description">
        <p>{{ playlist?.name }}</p>
        <div class="author">
          <img
            v-if="playlist?.coverImgUrl"
            :src="`${playlist?.creator?.avatarUrl}?imageView&thumbnail=360x0`"
            width="30"
            alt=""
          />
          <i class="type"></i>
          <span>{{ playlist?.creator.nickname }}</span>
        </div>
      </div>
    </header>

    <!--  -->
    <!--  -->
    <SongsDetails
      :singlesongs="singlesongs"
      @update-song="$emit('update-song', $event)"
      @update-song-list="$emit('update-song-list', $event)"
      :playing="playing"
      :playingId="playingId"
    >
    </SongsDetails>
    <!--  -->
    <CommentsDetails
      :hotcomments="hotcomments"
      :newcomments="newcomments"
      :total="total"
    >
    </CommentsDetails>
  </div>
</template>

<script>
import SongsDetails from "@/components/SongsDetails.vue";
import CommentsDetails from "@/components/CommentsDetails.vue";
export default {
  props: {
    playing: Boolean,
    playingId: [String, Number],
  },
  components: {
    SongsDetails,
    CommentsDetails,
  },
  data: function () {
    return {
      playlist: null,
      singlesongs: [],
      hotcomments: [],
      newcomments: [],
      total: null,
    };
  },
  computed: {
    playcount: function () {
      if (this.playlist) {
        return (this.playlist.playCount / 10000).toFixed(1).split(".")[1] == 0
          ? (this.playlist.playCount / 10000).toFixed(0)
          : (this.playlist.playCount / 10000).toFixed(1);
      } else {
        return null;
      }
    },
    fromPath() {
      return sessionStorage.getItem("fromPlay");
    },
  },
  beforeRouteEnter(to, from, next) {
    if (to.path != "/lyric" && from.path != "/lyric") {
      let a = "";
      for (var q in from.query) {
        a += `${q}=${from.query[q]}&`;
      }
      if (a.length > 0) {
        sessionStorage.setItem("fromPlay", from.path + "?" + a);
      } else {
        sessionStorage.setItem("fromPlay", from.path);
      }
    }
    next();
  },
  methods: {
    getPlayList: function (id) {
      this.axios
        .get("https://apis.netstart.cn/music/playlist/detail", {
          params: { id },
        })
        .then((res) => {
          this.playlist = res.data.playlist;
          this.singlesongs = res.data.playlist?.tracks;
        });
    },
    getComments: function (id) {
      this.axios
        .get("https://apis.netstart.cn/music/comment/playlist", {
          params: { id, limit: 10 },
        })
        .then((res) => {
          this.hotcomments = res.data.hotComments;
          this.newcomments = res.data.comments;
          this.total = res.data.total;
        });
    },
  },

  created: function () {
    this.getPlayList(this.$route.query.id);
    this.getComments(this.$route.query.id);
  },
};
</script>

<style lang="less" scoped>
.playlist {
  overflow: hidden;

  .playlist-title {
    padding: 15em 10rem 5rem;
    color: white;
    position: relative;

    span {
      font-size: 20rem;
      display: flex;
      justify-content: center;
      margin: 0 auto;
    }

    i.fa-reply {
      position: absolute;
      top: 20rem;
      left: 13rem;
      font-size: 20rem;
    }
  }

  .bg {
    width: 100vw;
    height: 100vh;
    position: absolute;
    z-index: -1;
    filter: blur(20rem);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center -200rem;
    transform: scale(1.5);

    &::after {
      content: "";
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.319);
      position: absolute;
    }
  }

  header {
    padding: 10rem 15rem 30rem;
    display: flex;

    .cover {
      position: relative;

      i {
        display: block;
        position: absolute;
        top: 12rem;
        left: 0;
        background: rgba(217, 48, 48, 0.8);
        border-top-right-radius: 10rem;
        border-bottom-right-radius: 10rem;
        color: white;
        font-size: 12rem;
        padding: 1rem 10rem;
      }

      span.earphone {
        position: absolute;
        right: 0;
        top: 0;
        display: block;
        padding: 3rem 5rem 0 14rem;
        color: white;
        background-image: url("data:image/svg+xml;base64,");
        background-repeat: no-repeat;
        background-size: 12rem;
        background-position: left 6rem;
      }
    }

    .description {
      margin-left: 10rem;
      font-size: 17rem;
      padding-top: 5rem;
      color: white;

      .author {
        margin-top: 3rem;
        display: flex;
        align-items: center;
        font-size: 12rem;
        position: relative;

        img {
          border-radius: 50rem;
          margin-right: 5rem;
        }

        i.type {
          display: block;
          position: absolute;
          bottom: 0;
          left: 23rem;
          width: 12rem;
          height: 12rem;
          background-image: url("../assets/usericn_2x.png");
          background-position: -40rem 0;
          background-size: 75px 25px;
          background-repeat: no-repeat;
        }

        span {
          color: hsla(0, 0%, 100%, 0.7);
        }
      }
    }
  }
}
</style>
