<template>
  <div class="playlyric">
    <ul ref="lyricTem" @mousewheel="xxx" v-if="songLyrics">
      <li
        ref="li"
        v-for="(item, index) in songLyrics"
        :key="index"
        :style="
          currentTime > item.time &&
          (index == songLyrics.length - 1
            ? true
            : currentTime < songLyrics[index + 1]?.time) &&
          `animation-duration: ${meiyiju}s;`
        "
        :class="{
          load:
            currentTime > item.time &&
            (index == songLyrics.length - 1
              ? true
              : currentTime < songLyrics[index + 1]?.time),
          playing,
        }"
      >
        {{ item.text }}
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    lyricID: [Number, String],
    currentTime: Number,
    playing: Boolean,
  },
  data() {
    return {
      songLyrics: null,
      reveLyric: null,
      indexState: true,
      a: true,
      meiyijudata: 0,
    };
  },
  computed: {
    liHeight: function () {
      return this.$refs?.li[0].offsetHeight;
    },

    meiyiju() {
      return (
        this.songLyrics[
          this.meiyijudata + 1 == this.songLyrics.length
            ? this.songLyrics.length
            : this.meiyijudata + 1
        ]?.time - this.songLyrics[this.meiyijudata]?.time
      );
    },
  },
  methods: {
    //
    getLyric(id) {
      this.axios
        .get("https://apis.netstart.cn/music/lyric", {
          params: {
            id,
          },
        })
        .then((res) => {
          var tmpLyrics = res.data.lrc.lyric.split("\n");
          this.songLyrics = tmpLyrics
            .filter((o) => o)
            .map((o) => {
              try {
                var regex = /\[(?<mm>\d{2}):(?<ss>\d{2}\.\d{2,3})\](?<text>.*)/;
                var { groups } = o.match(regex);
                return {
                  time: groups.mm * 60 + groups.ss * 1,
                  text: groups.text.trim(),
                };
              } catch {
                return "";
              }
            })
            .filter((e) => e !== "");
          this.reveLyric = this.songLyrics.slice().reverse();
        });
    },
    xxx() {
      this.a = false;
      clearTimeout(this.time);
      this.time = setTimeout(() => {
        this.a = true;
      }, 2000);
    },
  },
  created() {
    this.getLyric(this.lyricID);
  },
  watch: {
    currentTime(n) {
      if (
        this.indexState &&
        typeof this.$refs?.lyricTem?.scrollTop == "number" &&
        this.a
      ) {
        let i = this.reveLyric?.findIndex((e) => e.time <= n);
        this.meiyijudata = this.reveLyric?.length - 1 - i;
        this.$refs.lyricTem.scrollTop =
          (this.reveLyric?.length - 1 - i) * this.liHeight;
      }
    },
    lyricID(n) {
      this.getLyric(n);
    },
  },
};
</script>

<style lang="less" scoped>
@keyframes scan {
  0% {
    background-size: 0 100%;
  }
  100% {
    background-size: 100% 100%;
  }
}
.playlyric {
  padding: 40rem 80rem 20rem;
  overflow: hidden;
  ul {
    height: 350rem;
    overflow-y: auto;
    scrollbar-width: none;
    padding: 120rem 0;
    &::-webkit-scrollbar {
      display: none;
    }
    li {
      text-align: center;
      font-size: 14rem;
      padding: 5rem 0;
      height: 46rem;
      line-height: 40rem;
      // 
      background: hsla(0, 0%, 100%, 0.6) -webkit-linear-gradient(
          left,
          #fff,
          #fff
        ) no-repeat 0 0;
      // 
      -webkit-text-fill-color: transparent;
      // 
      -webkit-background-clip: text;
      // 0
      background-size: 0 100%;
      &.load {
        background-size: 100% 100%;
        animation: scan ease-in-out;
        animation-delay: -0.3s;
        animation-play-state: paused;
      }
      &.playing {
        animation-play-state: running;
      }
    }
  }
}
</style>