<template>
  <li
    class="song-item"
    @click="
      $emit('update-song', song);
      $emit('update-song-list', newsongs);
    "
    :class="{ active: playingId == song.id }"
  >
    <span class="song-name">{{ song.name }}</span>
    <p>
      <span class="sq" v-if="song.song.sqMusic"></span
      >{{ song.song.artists[0].name }} - {{ song.name }}
    </p>
    <span v-if="playingId !== song.id" class="play"> </span>
    <span v-else class="play active" :class="{ playing: playing }">
      <span></span>
      <span></span>
      <span></span>
      <span></span>
    </span>
  </li>
</template>

<script>
export default {
  props: {
    newsongs: Array,
    song: Object,
    playing: Boolean,
    playingId: [String, Number],
  },
  methods: {},
};
</script>

<style lang="less" scoped>
@keyframes playingrunning {
  from {
    transform: rotateX(15deg);
  }

  to {
    transform: rotateX(60deg);
  }
}

li.song-item {
  padding: 8rem 0;
  border-bottom: 1rem solid rgba(231, 229, 229, 0.486);
  margin-left: 10rem;
  position: relative;

  &.active {
    color: #d43c33;
  }

  span.song-name {
    font-size: 15rem;
  }

  span.play {
    display: block;
    background-image: url("../assets/index_icon_2x.png");
    background-position: -24px 0;
    background-size: 166px 97px;
    width: 22rem;
    height: 22rem;
    position: absolute;
    right: 10rem;
    top: 50%;
    margin-top: -11rem;

    &.active {
      background-image: none;
      display: flex;
      justify-content: space-around;

      span {
        width: 11%;
        height: 100%;
        background: #d43c33;
        animation: playingrunning 0.6s infinite alternate;
        animation-play-state: paused;
        transform-origin: bottom;

        &:nth-child(1) {
          animation-delay: 0s;
        }

        &:nth-child(2) {
          animation-delay: -0.2s;
        }

        &:nth-child(3) {
          animation-delay: -0.4s;
        }

        &:nth-child(4) {
          animation-delay: -0.6s;
        }
      }

      &.playing {
        span {
          animation-play-state: running;
        }
      }
    }
  }

  span.playing {
    background-size: cover;
    background-image: url("../assets/playing.gif");
  }

  span.sq {
    display: inline-block;
    margin-right: 3rem;
    background: url("../assets/index_icon_2x.png") no-repeat 0 0;
    background-size: 166px 97px;
    width: 12px;
    height: 8px;
  }

  p {
    color: rgba(0, 0, 0, 0.447);
    margin-top: 5rem;
    width: 80%;
  }
}
</style>
