<template>
  <li class="recom-li">
    <p style="color: rgba(0, 0, 0, 0.477)">{{ index + 1 }}</p>
    <div>
      <h2
        @click="
          $emit('update-song', n);
          $emit('update-song-list', hotSong);
        "
        :class="{ active: playingId == n.id }"
      >
        {{ n.name }} <span v-if="n.alia.length">({{ yyy(n.alia) }})</span>
      </h2>
      <p>
        <span v-if="n.sq ? true : false"></span>{{ xxx(n.ar) }} -
        {{ n.al.name }}
      </p>
    </div>

    <span v-if="playingId !== n.id" class="play"> </span>
    <span v-else class="play active" :class="{ playing: playing }">
      <span></span>
      <span></span>
      <span></span>
      <span></span>
    </span>
  </li>
</template>
  
  <script>
export default {
  props: {
    n: Object,
    index: Number,
    playing: Boolean,
    playingId: [String, Number],
    hotSong: [Object, Array],
  },
  methods: {
    xxx(ar) {
      let a = "";
      if (ar.length > 1) {
        ar.forEach((e) => {
          a += e.name + " / ";
        });
        return a;
      } else {
        return (a = ar[0]?.name);
      }
    },
    yyy(ar) {
      let a = "";
      ar.forEach((e) => {
        a += e + " ";
      });
      return a;
    },
  },
};
</script>
  
  <style lang="less" scoped>
.recom-li {
  display: flex;
  justify-content: space-between;
  padding: 6rem 0;
  align-items: center;
  border-bottom: 0.1rem solid rgba(221, 221, 221, 0.656);
  > div {
    width: 100%;
    font-size: 17rem;
    color: #333;
    h2 {
      height: 25.5rem;
      width: calc(375rem - 16rem - 22rem - 43.5rem);
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      > span {
        color: #888;
      }
      &.active {
        color: #d43c33;
      }
    }
    > p {
      font-size: 12rem;
      color: #888;
      height: 18rem;
      width: calc(375rem - 16rem - 22rem - 43.5rem);
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      > span {
        display: inline-block;
        width: 12rem;
        height: 8rem;
        margin-right: 4rem;
        background-image: url("../assets/index_icon_2x.png");
        background-size: 166rem 97rem;
        background-repeat: no-repeat;
      }
    }
  }
  span.play {
    flex-shrink: 0;
    width: 22rem;
    height: 22rem;
    background-position: -24rem 0;
    margin: 0 10rem;
    background-image: url("../assets/index_icon_2x.png");
    background-size: 166rem 97rem;
    background-repeat: no-repeat;
    &.active {
      background-image: none;
      display: flex;
      justify-content: space-around;
      > span {
        flex-shrink: 0;
        width: 11%;
        height: 100%;
        background: #d43c33;
        animation: playingrunning 0.6s infinite alternate;
        animation-play-state: paused;
        transform-origin: bottom;
        &:nth-child(1) {
          animation-delay: 0s;
        }
        &:nth-child(2) {
          animation-delay: -0.2s;
        }
        &:nth-child(3) {
          animation-delay: -0.4s;
        }
        &:nth-child(4) {
          animation-delay: -0.6s;
        }
      }

      &.playing {
        span {
          animation-play-state: running;
        }
      }
    }
  }
  > p {
    flex-shrink: 0;
    width: 40rem;
    height: 43.5rem;
    font-size: 17rem;
    text-align: center;
    line-height: 43.5rem;
  }
}
</style>
  