<template >
  <div class="albumlist">
    <!--  -->
    <div
      class="bg"
      :style="'backgroundImage:url(' + `${album?.blurPicUrl}?imageView&thumbnail=360x0` + ')'"
    ></div>
  <div class="albumlist-title">
      <i class="fa fa-reply" @click="$router.push('/search')" />
      <span></span>
    </div>
    <!--  -->
    <header>
      <!--  -->
      <div class="cover">
        <span class="album_bottom"></span>
        <img :src="`${album?.blurPicUrl}?imageView&thumbnail=360x0`" width="126" alt="" />
      </div>
      <!--  -->
      <div class="description">
        <p>{{ album?.name }}</p>
        <div class="author">
          <p>
            <span>{{ album?.artists[0].name }}</span>
          </p>
          <p>
            {{ publishtime.split("/")[0] }}-{{
              publishtime.split("/")[1].padStart(2, "0")
            }}-{{ publishtime.split("/")[2].padStart(2, "0") }}
          </p>
        </div>
      </div>
    </header>

    <div
      class="album-description"
      @click="
        toggleClass == 'shou' ? (toggleClass = 'open') : (toggleClass = 'shou')
      "
    >
      
      <p :class="toggleClass">{{ album.description }}</p>
      <span class="down" v-show="toggleClass == 'shou'"></span>
    </div>

    <!--  -->
    <!--  -->
    <SongsDetails :singlesongs="singlesongs" :album="album" :playing="playing"
      :playingId="playingId" @update-song="$emit('update-song', $event)" @update-song-list="$emit('update-song-list', $event)"> </SongsDetails>
    <!--  -->
    <CommentsDetails
      :hotcomments="hotcomments"
      :newcomments="newcomments"
      :total="total"
    >
    </CommentsDetails>
  </div>
</template>

<script>
import SongsDetails from "@/components/SongsDetails.vue";
import CommentsDetails from "@/components/CommentsDetails.vue";
export default {
  components: {
    SongsDetails,
    CommentsDetails,
  },
  props: {
    playing: Boolean,
    playingId: [String, Number],
  },
  data: function () {
    return {
      album: [],
      singlesongs: [],
      hotcomments: [],
      newcomments: [],
      total: null,
      toggleClass: "shou",
    };
  },
  computed: {
    publishtime() {
      return new Date(this.album.publishTime).toLocaleDateString();
    },
  },
  methods: {
    getAlbum: function (id) {
      this.axios
        .get("https://apis.netstart.cn/music/album", {
          params: { id },
        })
        .then((res) => {
          this.album = res.data.album;
          this.singlesongs = res.data.songs;
        });
    },
    getComments: function (id) {
      this.axios
        .get("https://apis.netstart.cn/music/comment/album", {
          params: { id, limit: 10 },
        })
        .then((res) => {
          this.hotcomments = res.data.hotComments;
          this.newcomments = res.data.comments;
          this.total = res.data.total;
        });
    },
  },

  created: function () {
    this.getAlbum(this.$route.query.id);
    this.getComments(this.$route.query.id);
  },
};
</script>

<style lang="less" scoped>
.albumlist {
  overflow: hidden;
   .albumlist-title {
    padding: 15em 10rem 5rem;
    color: white;
    position: relative;
    span {
      font-size: 20rem;
      display: flex;
      justify-content: center;
      margin: 0 auto;
    }
    i.fa-reply {
      position: absolute;
      top: 20rem;
      left: 13rem;
      font-size: 20rem;
    }
  }
  .bg {
    width: 100vw;
    height: 100vh;
    position: absolute;
    z-index: -1;
    filter: blur(20rem);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center -200rem;
    transform:scale(1.5);

  }
  .album-description {
    padding: 10rem 20rem;
    font-size: 14rem;
    color: #666;
    background: #f8f8f8;
    position: relative;
    p {
      margin-top: 14rem;
      margin-bottom: 10rem;
      width: 85vw;
      &.shou {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
      &.open {
        white-space: pre-wrap;
      }
    }

    span {
      background-image: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABgAAAANCAQAAAAZZWZbAAAAkElEQVR4AY2RtQEDMQxFX+D4RsgA6cN4XGr/cQJyEzD9yvCeZIAVWxbEJKNhDQ3CiUUE3iE0UNAjnFniS674nVQnqlw8iiHuJN/+hQRbzBluJP8tryQWfLAXy2j/61Aa3H7clOb7pJSMFtyipABUip/9T55yR2hIqZnifihRpVX8aMGtyg1BODAnMglHdnb8CYQbEwGgBxiKAAAAAElFTkSuQmCC");
      position: absolute;
      bottom: 3px;
      right: 10rem;
      z-index: 3;
      width: 15px;
      height: 15px;
      background-position: 50%;
      background-repeat: no-repeat;
      background-size: 12px auto;
    }
  }
  header {
   padding: 10rem 15rem 30rem;
    display: flex;
    .cover {
      position: relative;
      i {
        display: block;
        position: absolute;
        top: 12rem;
        left: 0;
        background: rgba(217, 48, 48, 0.8);
        border-top-right-radius: 10rem;
        border-bottom-right-radius: 10rem;
        color: white;
        font-size: 12rem;
        padding: 1rem 10rem;
      }
      span.album_bottom {
        position: absolute;
        left: 0;
        top: 0;
        z-index: 1;
        height: 100%;
        padding-left: 115%;
        background: url(//s3.music.126.net/mobile-new/img/album@2x.png?4d5d63f=)
          100% no-repeat;
        background-size: contain;
      }
    }
    .description {
      margin-left: 30rem;
      font-size: 17rem;
      padding-top: 5rem;
      color: white;
      .author {
        margin-top: 30rem;
        font-size: 12rem;
        position: relative;
        img {
          border-radius: 50rem;
          margin-right: 5rem;
        }
        i.type {
          display: block;
          position: absolute;
          bottom: 0;
