          width: 12rem;
          height: 12rem;
          background-image: url("../assets/usericn_2x.png");
          background-position: -40rem 0;
          background-size: 75px 25px;
          background-repeat: no-repeat;
        }
        p {
          color: hsla(0, 0%, 100%, 0.7);
          font-size: 14rem;
          margin-bottom: 8rem;
          &:nth-child(2) {
            font-size: 12rem;
          }
        }
