<template>
  <div id="app">
    <router-view
      @update-song="
        selectSongs = $event;
        songId = $event.id;
      "
      @update-song-list="songlist = $event"
      :playing="playing"
      :playingId="selectSongs?.id"
      @song-play="$refs.audio?.play()"
      @song-pause="$refs.audio?.pause()"
      :duration="duration"
      :currentTime="currentTime"
      @update-time="$refs.audio.currentTime = $event"
      :model="model"
      @change-model="changeModel"
      @change-song="changeSong"
      @toggle-showPlayLyric="show = !show"
      @toggle-showPlayList="showPlayList = !showPlayList"
      :showPlayList="showPlayList"
      :show="show"
      :songId="selectSongs.id"
      :selectSongs="selectSongs"
    />

    <audio
      ref="audio"
      v-if="selectSongs"
      :src="songSrc"
      @play="playing = true"
      @pause="playing = false"
      autoplay
      @durationchange="duration = $event.target.duration"
      @timeupdate="currentTime = $event.target.currentTime"
      @ended="changeSong('next')"
    ></audio>

    <PlayBar
      v-if="selectSongs && !show"
      :selectSongs="selectSongs"
      :playing="playing"
      @song-play="$refs.audio?.play()"
      @song-pause="$refs.audio?.pause()"
      @click.native="show = !show"
      :duration="duration"
      :currentTime="currentTime"
      :model="model"
      @change-model="changeModel"
      @toggle-showPlayList="showPlayList = !showPlayList"
      :showPlayList="showPlayList"
    ></PlayBar>

    <PlayList
      :show="show"
      v-show="showPlayList"
      :songlist="songlist"
      :songId="selectSongs?.id"
      @update-song="selectSongs = $event"
      @toggle-showPlayList="showPlayList = !showPlayList"
      @delete-song="deleteSong"
    />

    <div v-if="selectSongs && !show" style="height: 60rem" />
  </div>
</template>

<script>
import PlayBar from "@/components/PlayBar.vue";
import PlayList from "@/components/PlayList.vue";

export default {
  data: function () {
    return {
      selectSongs: JSON.parse(localStorage.getItem("selectSongs")),
      // selectSongs: null,
      playing: false,
      show: false,
      duration: 0,
      songId: JSON.parse(localStorage.getItem("songId")),
      currentTime: JSON.parse(localStorage.getItem("currentTime")),
      songlist: JSON.parse(localStorage.getItem("songlist")),
      showPlayList: false,
      models: ["sequence", "single", "random"],
      model: "sequence",
    };
  },
  components: {
    PlayBar,
    PlayList,
  },
  computed: {
    songSrc: function () {
      return this.selectSongs
        ? `https://music.163.com/song/media/outer/url?id=${this.selectSongs.id}.mp3`
        : null;
    },
  },
  watch: {
    currentTime(n) {
      localStorage.setItem("currentTime", JSON.stringify(n));
    },
    songlist: function (n) {
      localStorage.setItem("songlist", JSON.stringify(n));
    },
    selectSongs: function (n) {
      localStorage.setItem("selectSongs", JSON.stringify(n));
    },
    songId: function (n) {
      localStorage.setItem("songId", JSON.stringify(n));
    },
  },
  methods: {
    changeSong: function (direction) {
      this.showPlayList = false;
      switch (this.model) {
        case "sequence":
          var currentSongIndex = this.songlist.findIndex(
            (o) => o.id === this.selectSongs?.id
          );
          if (direction === "next") {
            this.selectSongs =
              currentSongIndex === this.songlist.length - 1
                ? this.songlist[0]
                : this.songlist[currentSongIndex + 1];
          } else {
            this.selectSongs =
              currentSongIndex === 0
                ? this.songlist[this.songlist.length - 1]
                : this.songlist[currentSongIndex - 1];
          }

          break;
        case "random":
          var daixuan = this.songlist.filter(
            (o) => o.id !== this.selectSongs?.id
          );
          this.selectSongs =
            daixuan[Math.floor(Math.random() * daixuan.length)];
          break;
        case "single":
          this.$refs.audio.currentTime = 0;
          this.$refs.audio.play();
          break;
      }
      this.songId = this.selectSongs.id;
      this.$router.push({
        path: "/lyric",
        query: {
          id: this.selectSongs.id,
        },
      });
    },
    changeModel: function () {
      var index = this.models.findIndex((o) => o === this.model);

      this.model =
        index === this.models.length - 1
          ? this.models[0]
          : this.models[index + 1];
    },
    deleteSong: function (n) {
      this.songlist = this.songlist.filter((o) => o.id !== n.id);
      if (n.id == this.songId) {
        this.changeSong("next");
      }
    },
  },
};
</script>

<style lang="less">
@keyframes playingrunning {
  from {
    transform: rotateX(15deg);
  }
  to {
    transform: rotateX(60deg);
  }
}
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  audio {
    position: absolute;
    top: 50%;
    left: 0;
  }
}
span.song-name {
  font-size: 15rem;
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 80vw;
}
li {
  &.active {
    color: #d43c33;
  }
}
</style>
