<template>
  <div class="hot">
    <div class="hot-title">
      <div class="title-img"></div>
      <p>{{ month }}{{ day }}</p>
    </div>
    <img v-if="loading" src="../assets/playing.gif" alt="" />

    <ul class="hot-songlist">
      <HotSong
        v-for="(hotsong, index) in hotsongs"
        :key="hotsong.id"
        :hotsong="hotsong"
        :hotsongs="hotsongs"
        :index="index"
        @update-song="$emit('update-song', $event)"
        @update-song-list="$emit('update-song-list', $event)"
        :playing="playing"
        :playingId="playingId"
      ></HotSong>
    </ul>
    <div class="more">
      <a href=""> ></a>
    </div>
  </div>
</template>

<script>
import HotSong from "@/components/HotSong.vue";

export default {
  props: {
    playing: Boolean,
    playingId: [String, Number],
  },
  data: function () {
    return {
      hotsongs: [],
      loading: false,
    };
  },
  computed: {
    month: function () {
      return new Date().getMonth() + 1;
    },
    day: function () {
      return new Date().getDate();
    },
  },
  components: {
    HotSong,
  },
  created() {
    this.loading = true;
    this.axios
      .get("https://apis.netstart.cn/music/playlist/detail?id=3778678")
      .then((res) => {
        this.hotsongs = res.data.playlist.tracks;
        this.hotsongs.length = 20;
        this.loading = false;
      });
  },
};
</script>


