<template>
  <div class="search">
    <!--  -->
    <div class="search-top">
      <span class="search-glass"></span>
      <input
        type="text"
        placeholder=""
        v-model="value"
        @keydown.enter="
          getResults(value);
          getMultimatch(value);
        "
      />
      <span
        v-show="multimatchs?.length"
        class="close"
        @click="
          value = '';
          multimatchs = null;
        "
      ></span>
    </div>

    <!-- / -->
    <SearchedList
      v-show="!searching?.length && !multimatchs?.length"
      :hots="hots"
      :historys="historys"
      @getResults="getResults($event)"
      @deleteHistorys="deleteHistorys($event)"
      :loading="loading"
    ></SearchedList>

    <!--  -->
    <SearchingList
      :value="value"
      :searching="searching"
      v-if="!multimatchs && searching?.length"
      @getResults="getResults($event)"
      :loading="loading"
    ></SearchingList>

    <!--  -->
    <MultimatchList
      v-if="multimatchs"
      :multimatchs="multimatchs"
      :value="value"
      @update-song="$emit('update-song', $event)"
      @update-song-list="$emit('update-song-list', $event)"
      :playing="playing"
      :playingId="playingId"
    ></MultimatchList>
  </div>
</template>

<script>
var _ = require("lodash");

import SearchedList from "@/components/SearchedList.vue";
import SearchingList from "@/components/SearchingList.vue";
import MultimatchList from "@/components/MultimatchList.vue";

export default {
  props: {
    playing: Boolean,
    playingId: [String, Number],
  },
  data: function () {
    return {
      value: "",
      hots: [],
      historys: JSON.parse(window.localStorage.getItem("historys")) || [],
      searching: null,
      multimatchs: [],
      loading: false,
    };
  },
  components: {
    SearchedList,
    SearchingList,
    MultimatchList,
  },

  computed: {},

  methods: {
    getResults(keywords) {
      this.loading = true;

      this.axios
        .get("https://apis.netstart.cn/music/cloudsearch", {
          params: {
            keywords,
            limit: 10,
          },
        })
        .then((res) => {
          this.loading = false;

          this.multimatchs = res.data.result.songs;
        });
      this.value = keywords;
      this.historys = [...new Set([keywords, ...this.historys])];
    },

    // api
    getMultimatch(keywords) {
      this.loading = true;

      this.axios
        .get("https://apis.netstart.cn/music/search/multimatch", {
          params: {
            keywords,
            limit: 10,
          },
        })
        .then((res) => {
          this.loading = false;

          this.album = res.data.result;
        });
    },

    getSearches(keywords) {
      this.loading = true;
      this.axios
        .get("https://apis.netstart.cn/music/search/suggest", {
          params: {
            keywords,
            type: "mobile",
          },
        })
        .then((res) => {
          this.loading = false;
          this.value
            ? (this.searching = res.data.result.allMatch)
            : (this.searching = []);
        });
    },

    deleteHistorys(newHistorys) {
      this.historys = this.historys.filter((o) => o !== newHistorys);
    },
  },

  created() {
    this.loading = true;
    this.debouncedGetSearches = _.debounce(this.getSearches, 500);

    this.axios.get("https://apis.netstart.cn/music/search/hot").then((res) => {
      this.loading = false;
      this.hots = res.data.result.hots;
    });
  },

  watch: {
    value: function (newValue) {
      if (newValue) {
        this.debouncedGetSearches(newValue);
        this.multimatchs = null;
      } else {
        this.searching = [];
      }
    },

    historys: function (newhistorys) {
      localStorage.setItem("historys", JSON.stringify(newhistorys));
    },
  },
};
</script>

<style lang="less">
.search {
  position: relative;
  top: 120rem;
  overflow: hidden;

  span {
    position: absolute;
    display: block;
    width: 15rem;
    height: 15rem;
    background-size: cover;
    background-position: center;
  }

  .search-top {
    border-bottom: 1rem solid rgba(0, 0, 0, 0.099);
    padding: 15rem 0;

    span.search-glass {
      left: 25rem;
      top: 21rem;
      background-image: url("data:image/svg+xml;base64,");
    }

    span.close {
      right: 25rem;
      top: 21rem;
      background-image: url("data:image/svg+xml;base64,");
    }

    input[type="text"] {
      border-radius: 20px;
      background: #ebecec;
      border: 0;
      padding: 6rem 30rem;
      width: 92vw;
      margin-left: 15rem;

      &:focus {
        outline: 0;
      }
    }
  }
}
</style>
