<template>
  <li
    class="recommend-item"
    @click="$router.push({ path: '/playlist', query: { id: recommend.id } })"
  >
    <span class="sift" v-if="recommend.highQyality"></span>
    <span class="earphone">{{ playcount + "" }}</span>
    <img
      :src="`${recommend.picUrl}?imageView=1&type=webp&thumbnail=247x0`"
      alt=""
    />
    <h5>{{ recommend.name }}</h5>
  </li>
</template>

<script>
export default {
  props: {
    recommend: Object,
  },
  computed: {
    playcount: function () {
      return (this.recommend.playCount / 10000).toFixed(1).split(".")[1] == 0
        ? (this.recommend.playCount / 10000).toFixed(0)
        : (this.recommend.playCount / 10000).toFixed(1);
    },
  },
};
</script>

<style lang="less" scoped>
li.recommend-item {
  width: 30%;
  flex-grow: 1;
  margin-bottom: 20rem;
  position: relative;

  &:nth-child(3n + 2) {
    margin: 0 5rem;
  }
  h5 {
    padding-left: 5rem;
    display: -webkit-box;
    overflow: hidden;
    text-overflow: ellipsis;
    word-wrap: break-word;
    white-space: normal !important;
    //2
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
  }
  span.sift {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 20rem;
    height: 20rem;
    background: url("data:image/svg+xml;base64,");
  }
  span.earphone {
    position: absolute;
    right: 0;
    top: 0;
    display: block;
    padding: 3rem 5rem 0 14rem;
    color: white;
    background-image: url("data:image/svg+xml;base64,");
    background-repeat: no-repeat;
    background-size: 12rem;
    background-position: left 6rem;
  }
}
</style>