<template>
  <div class="hot-search">
    <p></p>

    <ul class="searchHot-list">
      <li
        v-for="(hot, index) in hots"
        :key="index"
        @click="$emit('getResults', hot.first)"
      >
        {{ hot.first }}
      </li>
    </ul>

    <ul class="searchHistory-list">
      <li
        v-for="(history, index) in historys"
        :key="index"
        @click="$emit('getResults', history)"
      >
        <span class="searched-history"></span>
        {{ history }}
        <span
          class="searched-close"
          @click.stop="$emit('deleteHistorys', history)"
        ></span>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  props: {
    hots: Array,
    historys: Array,
    loading: Boolean,
  },
  data() {
    return {};
  },
};
</script>

